






























@import "@/scss/theme";

.aboutMe-content {
  max-width: 900px;
  text-align: left;
  padding: 0 30px;
  margin: 0 auto;
  p{
    font-size: 18px;
    margin: 0;
  }
  .education-content {
    white-space: pre-wrap;
  }
  .intro-mail, .intro-phone {
    text-decoration: none;
    color: $color-header-menu;
    &:hover {
      color: $color-intro-mail-hover;
      transition: .5s all;
    }
  }
}
