



























































@import "@/scss/theme";

.intro {
  color: $color-white;
  .first-block {
    display: flex;
    padding: 0 30px;
    max-width: 1200px;
    margin: 0 auto;
    .self-img {
      flex: 1;
      height: auto;
      text-align: center;
      -webkit-animation-duration: 2s;
      animation-duration: 2s;
      img {
        width: 100%;
        object-fit: cover;
      }
    }
    .self-intro {
      flex: 1;
      text-align: left;
      display: flex;
      justify-content: center;
      flex-direction: column;
      h3 {
        letter-spacing: 5px;
        color: $color-intro-h3;
      }
    }
  }
  @media only screen and (max-width: 650px) {
    .first-block {
      flex-direction: column;
    }
  }
  .second-block {
    width: 100%;
    // max-width: 1200px;
    margin: 0 auto;
    text-align: center;
    background: $color-intro-second-block-bg;
    padding-bottom: 20px;
    h1 {
      line-height: 150px;
      margin: 0;
      display: initial;
      position: relative;
      &::before {
        position: absolute;
        content: "";
        bottom: -10px;
        left: 0;
        right: 0;
        width: 70px;
        height: 3px;
        background: $color-header-menu;
        -webkit-transition: all 0.3s;
        transition: all 0.3s;
        margin: 0 auto;
      }
    }
  }
  .third-block {
    width: 100%;
    // max-width: 1200px;
    margin: 0 auto;
    text-align: center;
    background: $color-intro-third-block-bg;
    padding-bottom: 40px;
    h1 {
      line-height: 150px;
      margin: 0;
      display: initial;
      position: relative;
      &::before {
        position: absolute;
        content: "";
        bottom: -10px;
        left: 0;
        right: 0;
        width: 40px;
        height: 3px;
        background: $color-header-menu;
        -webkit-transition: all 0.3s;
        transition: all 0.3s;
        margin: 0 auto;
      }
    }
    .skill-content {
      display: flex;
    }
    @media only screen and (max-width: 650px) {
      .skill-content {
        flex-direction: column;
      }
    }
  }
}
@-webkit-keyframes fadeInRight {
  0% {
    opacity: 0;
    -webkit-transform: translateX(300px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
  }
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translateX(300px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.fadeInRight {
  -webkit-animation-name: fadeInRight;
  animation-name: fadeInRight;
}
