































@import "@/scss/theme";

.follow-me {
  margin-bottom: 10px;
}
a {
  text-decoration: none;
  color: $color-intro-follow-me-icon;
  i {
    font-size: 1.3em !important;
  }
  &:first-child {
    margin-left:0;
  }
  & + a {
    margin-left: 15px;
  }
  &:hover {
    color: $color-header-menu;
    transition: all .5s;
  }
}
